<template>
  <div id="app" v-cloak>
    <header id="header">
      <div id="logo">Desks</div>
      <div id="user-info" v-if="logComplete">
        <h5>Hi, {{login}}</h5>
        <button class="btn btn-danger" v-on:click="signOut" name="button">Log out</button>
      </div>
    </header>
    <div v-if="logComplete" id="logged-user-workplace">
      <workplace id="panel" :uid="uid"></workplace>
    </div>
      <log-in v-if="!logComplete" @addUser="logComplete = $event.logComplete, login = $event.email, uid = $event.uid"></log-in>
  </div>
</template>

<script>
  import logIn from './components/logIn.vue'
  import signup from './components/Signup.vue'
  import workplace from './components/Workplace.vue'
  export default {
    name: 'app',
    data() {
      return {
        logComplete: false,
        login: '',
        uid: '',
        sideShow: false
      }
    },
    components: {
      logIn,
      workplace,
      signup
    },
    methods: {
      switchSign(currentSign) {
        this.sign = currentSign;
      },
      signOut() {
        this.logComplete = false;
        firebase.auth().signOut().then(function() {
          
        }).catch(function(error) {
          console.log(error.message)
        });
      }
    }
  }
</script>

<style>
  body {
    background: #1355ac;
    padding: 0;
    margin: 0;
    width: 100%;
    height: 100%;
  }
  .button-change,
  .button-delete,
  .button-change-complete {
    border: none;
    background: transparent;
    color: #666;
    font-weight: bold;
    position: absolute;
    top: 15px;
    right: 10px;
    height: 20px;
    font-size: 15px;
    line-height: normal;
  }
  .button-change {
    right: 30px;
    color: #e4b118;
  }
  .button-change:hover {
    color: #e4b118;
  }
  .button-delete {
    right: 5px;
  }
  .button-delete:before {
    content: 'X';
  }
  .button-delete:hover {
    color: #771111;
  }
  .button-change-complete {
    color: #1f6c08;
  }
  .button-change-complete:hover {
    color: green;
  }
  #header {
    position: relative;
    background: #fff;
    font-size: 25px;
    height: 50px;
    line-height: 50px;
    padding: 0 10px;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    position: fixed;
    top: 0;
    right: 0;
    left: 0;
    z-index: 10;
    border-bottom: 1px #666 solid;
  }
  #header #logo {
    position: fixed;
    display: inline-block;
    width: 100px;
    right: 50%;
    margin-right: -50px;
    text-align: center;
  }
  #header #logo.logged {
    left: 0;
  } 
  #header #login {
    display: inline-block;
  }
  #header #user-info {
    float: right;
  }
  #header #user-info h5 {
    margin: 0;
    font-size: 12px;
    display: inline-block;
  }
  #header input {
    height: 30px;
    width: 200px;
    font-size: 12px;
  }
  #unlogged-user-workplace h1 {
    text-align: center;
    position: absolute;
    height: 100px;
    top: 50%;
    width: 500px;
    right: 50%;
    margin-right: -250px;
    margin-top: -50px;
    background: #fff;
    line-height: 100px;
    -webkit-border-radius: 6px;
            border-radius: 6px;
  }
  [v-cloak] {
    display: none;
  }
@media screen and (max-width: 450px) {
  #header #user-info h5 {
    display: none;
  }
}
</style>



