<template id="">
    <div id="main-wrapper">
      <div v-if="!reg" id="login-wrapper">
        <h4 class="text-center">Log in</h4>
        <form @submit.prevent="loginUser" action="#" id="login-form">
          <input v-on:keypress="user.signin.error.email = false" v-model="user.signin.email" placeholder="E-mail" type="email">
          <input v-on:keypress="user.signin.error.password = false" v-model="user.signin.password" placeholder="Password" type="password">
          <button>Log in</button>
          <div id="email-error" v-if="user.signin.error.email" class="error">Invalid user</div>
          <div id="pass-error" v-if="user.signin.error.password" class="error">Invalid Password</div>
        </form>
        <h5 class="text-center">Or register an account</h5>
        <button v-on:click="reg=true" id="reg-btn">Register</button>
        <div v-if="registered" class="alert alert-success"><strong>Yep!</strong> An account has been registered! Use login form to sign in</div>
      </div>
      <div v-if="reg" id="signup-wrapper">
        <div id="signup-form">
          <h4 class="text-center">Sign up</h4>
          <form @submit.prevent="registerUser" action="#">
            <input v-on:keypress="user.signup.error.email = false" placeholder="E-mail" v-model="user.signup.email" type="email">
            <input v-on:keypress="user.signup.error.password = false" placeholder="Password" v-model="user.signup.password" type="password">
            <input v-on:keypress="user.signup.error.confirmPassword = false" placeholder="confirmPassword" v-model="user.signup.confirmPassword" type="password">
            <button>Sign up</button>
            <div v-if="user.signup.error.email" id="email-error" class="error">This email is already regisered</div>
            <div v-if="user.signup.error.password" id="pass-error" class="error">Password must be at least 6 symbols</div>
            <div v-if="user.signup.error.confirmPassword" id="cpass-error" class="error">Passwords do not match</div>
          </form>
        </div>
        <button v-on:click="reg = false" id="back">
          <i class="fa fa-arrow-left" aria-hidden="true"></i>
            Back to log in
          </button>
      </div>
    </div>

</template>

<script>
  export default {
    name: 'log-in',
    data() {
      return {
        user: {
          signin: {
            email: '',
            password: '',
            error: {
              email: false,
              password: false
            }
          },
          signup: {
            email: '',
            password: '',
            confirmPassword: '',
            error: {
              email: false,
              password: false,
              confirmPassword: false
            }
          }
        },
        reg: false,
        registered: false
      }
    },
    ready() {
      this.user.signin.email = ''
    },
    methods: {
      loginUser () {
        
        firebase.auth().signInWithEmailAndPassword(this.user.signin.email, this.user.signin.password)
          .then(response => {
            const settings = {
                email: response.email,
                uid: response.uid,
                logComplete: true
            }
            this.$emit('addUser', settings)
          })
          .catch(error => {
            if (error.code == "auth/invalid-email" || error.code == "auth/user-not-found") {
              this.user.signin.error.email = true;
            }
            else if (error.code == "auth/wrong-password") {
              this.user.signin.error.password = true;
            }
            console.log(error);
          })
      },
      registerUser() {
            if(this.user.signup.password != this.user.signup.confirmPassword) {
              this.user.signup.error.confirmPassword = true;
              if (this.user.signup.password.length < 6) {
                this.user.signup.error.password = true;
              }
            }
            
            else {
              firebase.auth().createUserWithEmailAndPassword(this.user.signup.email, this.user.signup.password).catch(error => {console.log(error.message)});

                this.reg = false;
                this.user.signup.error.email = false;
                this.user.signup.error.password = false;
                this.user.signup.error.confirmPassword = false;
                this.registered = true;
              }
            }
    }
  }
</script>

<style>
  #login-wrapper {
    padding: 10px;
    padding-bottom: 30px;
    background: #fff;
    width: 300px;
    position: absolute;
    top: 50%;
    right: 50%;
    box-sizing: content-box;
    margin-top: -100px;
    margin-right: -160px;
  }
  #signup-wrapper {
    background: #fff;
    padding: 10px 10px 35px;
    width: 300px;
    box-sizing: content-box;
    position: absolute;
    top: 50%;
    right: 50%;
    margin-top: -100px;
    margin-right: -160px;
  }
  #signup-form form {
    position: relative;
  }
  #signup-wrapper button {
    width: 300px;
    height: 40px;
    background: #0baf38;
    color: #fff;
    font-weight: bold;
    border: none;
  }
  #login-wrapper .alert {
    position: absolute;
    bottom: -100px;
    left: 0;
    width: 100%;
  }
  #login-form {
    position: relative;
  }
  .error {
    position: absolute;
    background: #c9211d;
    padding: 10px 0;
    width: 300px;
    text-align: center;;
    color: #fff;
    font-weight: bold;
    border-radius: 6px;
    left: -320px;
  }
  #email-error {
    top: 0;
  }
  #pass-error {
    top: 50px;
  }
  #cpass-error {
    top: 100px;
  }
  .error:after {
    content: ''; 
    position: absolute;
    right: -28px; top: 10px;
    border: 10px solid transparent;
    border-left: 20px solid #c9211d;
  }
  
  #main-wrapper input,
  #login-form button {
    width: 300px;
    height: 40px;
    display: block;
    border: none;
    background: #f1f1f1;
    border-radius: 0;
    margin-bottom: 10px;
  }
  #main-wrapper input {
    padding-left: 5px;
    border: 1px #c8c8c8 solid;
  }
  #login-form button {
    margin: 0;
    background: #1355ac;
    color: #fff;
    font-weight: bold;
  }
  #login-form button:active {
    background: #1365ac;
  }
  #main-wrapper h4 {
    margin: 0 0 10px;
  }
  h5 {
    margin-top: 20px;
  }
  #reg-btn {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 30px;
    background: #0baf38;
    border: none;
    color: #fff;
  }
  #reg-btn:active {
    background: #10b627;
  }
  button#back {
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    width: 100%;
    height: 25px;
    background: #1355ac;
    border: 2px #fff solid;
    border-top: 0;
  }
</style>



