<template id="panel">
  <div v-on:keyup.27="show = false" id="workplace">
    <button id="side" v-on:click="show = !show">
       <i class="fa fa-bars" aria-hidden="true"></i>
    </button>
    <transition  name="slide-fade">
      <aside class="col-xs-12"  v-if="show" id="sidebar">
        <div id="sidebar-add-desk">
            <input @keyup.13="addDesk()" v-model="new_desk" type="text" placeholder="Desk name">
            <button @click.prevent="addDesk()" class="btn btn-success">Add desk</button>
        </div>
        <hr>
        <div id="sidebar-desks-list">
          <ul>
            <li v-bind:class="{deskeditable: desk.editable}" class="li" v-for="desk, index in data.desksArray">
              <h5 v-on:click="changeDeskName(new_desk_name, index)" v-if="!desk.editable">{{desk.name}}</h5>
              <input v-on:keyup.13="completeChanges(new_desk_name, index)" v-if="desk.editable" type="text" v-model="new_desk_name">
              <button v-if="desk.editable" v-on:click="completeChanges(new_desk_name, index)" class="button-change-complete"><i class="fa fa-check"></i></button>
              <button v-if="!desk.editable" v-on:click="removeDesk(index)" class="button-delete"></button>
            </li>
          </ul>
        </div>
      </aside>
    </transition>  
    <div id="desks" class="col-xs-12">
      <div v-for="desk, index in data.desksArray" class="desk">

        <div v-bind:class="{deskeditable: desk.editable}" class="li desk-hdr">
          <h5 v-on:click="changeDeskName(new_desk_name, index)" v-if="!desk.editable">{{desk.name}}</h5>
          <input v-on:keyup.13="completeChanges(new_desk_name, index)" v-if="desk.editable" type="text" v-model="new_desk_name">
          <button v-if="desk.editable" v-on:click="completeChanges(new_desk_name, index)" class="button-change-complete"><i class="fa fa-check"></i></button>
          <button v-if="!desk.editable" v-on:click="removeDesk(index)" class="button-delete"></button>
        </div>

        <div class="desk-body">
          <ul>
            <li v-show="task.visible" v-bind:class="{editable: task.taskeditable}" v-for="task, i in data.desksArray[index].tasks">
              <p v-on:click="changeTaskName(new_task_name, i, index)" v-if="!task.taskeditable">{{task.taskname}}</p>
              <input v-on:keyup.13="completeTaskChange(new_task_name, i, index)" v-model="new_task_name" v-if="task.taskeditable" type="text">
              <button v-if="task.taskeditable" v-on:click="completeTaskChange(new_task_name, i, index)" class="button-change-complete"><i class="fa fa-check"></i></button>
              <!-- <button v-if="!task.taskeditable" v-on:click="changeTaskName(new_task_name, i, index)" class="button-change"><i class="fa fa-pencil"></i></button> -->
              <button v-if="!task.taskeditable" v-on:click="removeTask(index, i)" class="button-delete"></button>
            </li>
          </ul>
        </div>
        <div class="desk-footer">
          <form action="#">
            <input v-model="new_desk_task" type="text" placeholder="New task">
            <buttton @click.prevent="addDeskTask(new_desk_task, index)" class="add-task btn btn-success">Add task</buttton>
          </form>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'workplace',
  data() {
    return {
      new_desk: '',
      data: {
        desksArray: [
          {
            tasks: []
          }
        ],
      },
      show: false
    }
  },
  props: ['uid'],
  methods: {
    addDesk() {
        if (this.new_desk) {
          this.data.desksArray.push({name: this.new_desk, editable: false, tasks: []});
                   firebase.database().ref('users/' + this.uid).set({
                    data: this.data
                    });
          this.new_desk = '';
        }
    },
    removeDesk(index) {
      this.data.desksArray.splice(index, 1);

      firebase.database().ref('users/' + this.uid).set({
        data: this.data
      });
    },
    addDeskTask(new_desk_task, index){
      if (this.new_desk_task) {
        if(!this.data.desksArray[index].tasks) {
            this.data.desksArray[index].tasks = []
          }
          this.data.desksArray[index].tasks.push({taskname: new_desk_task, taskeditable: false, visible: true});
          this.new_desk_task = '';
      };
      console.log(this.data.desksArray[index].tasks);

      firebase.database().ref('users/' + this.uid).set({
        data: this.data
      });
    },
    changeDeskName(new_desk_name, index) {
      for (var i = 0; i < this.data.desksArray.length; i++) {
        this.data.desksArray[i].editable = false;
        }
        this.new_desk_name = this.data.desksArray[index].name;
        this.data.desksArray[index].editable = true;

        firebase.database().ref('users/' + this.uid).set({
          data: this.data
        });
    },
    completeChanges(new_desk_name, index) {
        this.data.desksArray[index].name = this.new_desk_name;
        this.data.desksArray[index].editable = false;

        firebase.database().ref('users/' + this.uid).set({
          data: this.data
        });
    },
    changeTaskName(new_task_name, i, index) {
      this.new_task_name = this.data.desksArray[index].tasks[i].taskname;
      this.data.desksArray[index].tasks[i].taskeditable = true;

      firebase.database().ref('users/' + this.uid).set({
        data: this.data
      });
    },
    completeTaskChange(new_task_name, i, index) {
      this.data.desksArray[index].tasks[i].taskname = this.new_task_name;
      this.data.desksArray[index].tasks[i].taskeditable = false;

      firebase.database().ref('users/' + this.uid).set({
        data: this.data
      });
    },
    removeTask(index, i) {
      this.data.desksArray[index].tasks.splice(i, 1);

      firebase.database().ref('users/' + this.uid).set({
        data: this.data
      });
    },
    minimize(index, i) {
      for (let i = 0; index < this.data.desksArray[index].tasks.length; i++) {
          this.data.desksArray[index].tasks[i].visible = !this.data.desksArray[index].tasks[i].visible;
      }
      this.data.minBtn.isUp = !this.data.minBtn.isUp;
      console.log(this.data.isUp);
    }
  },
  created() {
    this.data.desksArray = [];

    const takeDesks = firebase.database().ref('users/' + this.uid + '/data');
    takeDesks.on('value', (snapshot) => {
      if (snapshot.val().desksArray) {
        this.data = snapshot.val();
      } else {
        this.data.desksArray = [];
      }
    });
  }
}
</script>

<style>

  #workplace input {
    background: #fff;
    border: 1px #999 solid;
    padding: 6px 5px;
    border-radius: 6px; 
  }

  #side {
    position: fixed;
    top: 0;
    left: 0;
    z-index: 100;
    border: none;
    background: transparent;
    line-height: 50px;
    font-size: 25px;
  }
  #sidebar {
    z-index: 100;
    box-shadow: 0 10px 10px;
    padding: 0;
    background: #fff;
    position: fixed;
    left: 0;
    top: 50px;
    bottom: 0;
    max-width: 350px;
    
  } 
  .slide-fade-enter-active {
  transition: all .3s ease;
}
.slide-fade-leave-active {
  transition: .3s ease;
}
.slide-fade-enter, .slide-fade-leave-to
{
  transform: translateX(-350px);
  opacity: 1;
}
  #sidebar #sidebar-add-desk {
    padding: 10px;
    margin: 10px 0 20px;
  }
  #sidebar #sidebar-add-desk input {
    padding: 6px 10px;
    border-radius: 6px;
    border: none;
    border: 1px #c2c2c2 solid;
  }
  #sidebar #sidebar-add-desk:before {
    content: "Add a new desk";
    text-align: center;
    display: block;
    opacity: .4;
    margin-bottom: 10px;
  }
  #sidebar #sidebar-add-desk button,
  #sidebar #sidebar-add-desk input {
    width: 100%;
  }
  #sidebar #sidebar-add-desk button {
    margin-top: 5px;
  }
  #sidebar hr {
    margin: 0;
  }
  #sidebar #sidebar-desks-list ul {
    padding: 10px;
    padding-top: 0;
    height: 75vh;
    overflow-y: scroll;
    margin: 0;
    margin-top: 10px;
    width: 100%;
  }
  #sidebar #sidebar-desks-list ul li {
    list-style: none;
    background: #f7f7f7;
    border: 1px #e8e8e8 solid;
    width: 100%;
    padding: 25px 10px;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    -webkit-border-radius: 3px;
            border-radius: 3px;
    margin-bottom: 10px;
    position: relative;
    overflow-x: hidden;
  }
  #sidebar #sidebar-desks-list ul li.deskeditable {
    padding: 15.5px 10px;
    padding-left: 0;
  }
  #sidebar #sidebar-desks-list ul li.deskeditable input {
    margin-left: 3px;
  }
  #sidebar #sidebar-desks-list ul li button {
    -webkit-transition: .2s ease;
    -o-transition: .2s ease;
    transition: .2s ease;
    right: -30px;
  }
  #sidebar #sidebar-desks-list ul li:hover button {
    right: 10px;
  }
  #sidebar #sidebar-desks-list ul:empty::before {
    content: "You have no desks";
    opacity: .4;
    display: block;
    text-align: center;
  }
  #sidebar #sidebar-desks-list ul:not(:empty) {
    margin-top: 10px;
  }
  #sidebar #sidebar-desks-list ul:not(:empty):before {
    content: "Your desks";
    opacity: .4;
    display: block;
    text-align: center;
    margin-bottom: 10px;
  }
  #desks {
    margin-top: 55px;
  }
  #desks .desk {
    position: relative;
    vertical-align: top;
    display: inline-block;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    background: #fff;
    margin: 5px;
    width: 300px;
    -webkit-border-radius: 4px;
            border-radius: 4px;
  }
  #desks .desk .desk-hdr {
    -webkit-border-radius: 4px 4px 0 0;
            border-radius: 4px 4px 0 0;
    background: #ebebeb;
    padding: 17px;
    padding-bottom: 18px;
    padding-left: 21px;
    position: relative;
  }
  .li h5 {
    margin: 0;
  }
  .li h5:hover {
    color: #666;
    cursor: pointer;
  }
  .li  input {
    padding: 6px;
    border: none;
    border: 1px #c2c2c2 solid;
    margin-left: 6px;
    border-radius: 6px;
  }
  .li.deskeditable {
    padding: 8px;
  }
  #desks .desk .desk-body {
    max-height: 300px;
    overflow-y: auto;
  }
  #desks .desk .desk-body #minimizer-btn {
    display: block;
    margin: 0 auto;
    background: transparent;
    border: none;
    font-size: 25px;
  }
  #desks .desk .desk-body ul {
    -webkit-transition: .3s ease;
    -o-transition: .3s ease;
    transition: .3s ease;
    margin: 0;
    padding: 10px 10px 10px;
    width: 100%;
  }
  #desks .desk .desk-body ul:empty {
    padding: 0;
  }
  #desks .desk .desk-body ul li {
    position: relative;
    background: #f7f7f7;
    border: 1px #e8e8e8 solid;
    -webkit-border-radius: 3px;
            border-radius: 3px;
    margin-bottom: 5px;
    padding: 15px;
    padding-left: 21px;
    list-style: none;
    overflow-x: hidden;
  }
  #desks .desk .desk-body ul li:hover .button-delete {
    right: 10px;
  }
  #desks .desk .desk-body ul li .button-delete {
    -webkit-transition: .2s ease;
    -o-transition: .2s ease;
    transition: .2s ease;
    right: -35px;
  }
  #desks .desk .desk-body ul li p {
    width: 85%;
    word-wrap: break-word;
    margin: 0;
    padding: 0;
  }
  #desks .desk .desk-body ul li p:hover {
    color: #666;
    cursor: pointer;
  }
  #desks .desk .desk-body ul li input {
    width: 85%;
    overflow: auto;
    word-wrap: break-word;
    padding: 5px;
    border: none;
    border: 1px #c2c2c2 solid;
    border-radius: 6px;
    padding: 6px;
    margin-left: 6px;
  }
  #desks .desk .desk-body ul li.editable {
    padding: 8px;
  }
  #desks .desk .desk-footer {
    background: #ebebeb;
    padding: 10px;
    -webkit-border-radius: 0 0 4px 4px;
            border-radius: 0 0 4px 4px;
  }
  #desks .desk .desk-footer input {
    display: inline-block;
    width: 195px;
    margin-bottom: 5px;
    padding: 6px 10px;
    border-radius: 6px;
    border: none;
    border: 1px #c2c2c2 solid;
    box-sizing: border-box;
  }
  #desks .desk .desk-footer .add-task {
    display: inline-block;
    width: 80px;
    padding: 5px;
  }

  @media screen and (max-width: 768px) {
    
    #desks {
      padding: 0 10px;
    }
    #desks .desk {
      width: 100%;
      margin-left: 0;
    }
  }
  @media screen and (max-width: 450px) {
    #header #logo {
      width: 100px;
    }
  }
</style>



