<template>
	 <div class="col-xs-8 col-xs-offset-2" id="wrapper">
		<div id="reg-wrapper">
	        <h1 class="text-center">Register to Desks</h1>
	        <form id="reg-form" @submit.prevent="registerUser" action="#">
	          <input v-model="user.semail" placeholder="E-mail" type="email">
	          <input v-model="user.spassword" placeholder="Password" type="password">
	          <input v-model="user.sconfirmPassword" placeholder="Confirm password" type="password">
	          <button class="btn btn-success">Register</button>
	          <div v-if="error" class="alert alert-danger">
	          	<strong>Oops!</strong>
	          	Seems like passwords do not match!
	          </div>
	          <div v-if="registered" class="alert alert-success">
	          	<strong>Yep!</strong>
	          	An account has been created.
	          </div>
	        </form>
	      </div>
	  </div>
</template>

<script>
	export default {
		name: "sign-up",
		data() {
			return {
				user: {
					semail: '',
					spassword: '',
					sconfirmPassword: ''
				},
				error: false,
				reg: false
			}
		},
		methods: {
			
		}
	}
</script>

<style>
	.btn-default {
		position: absolute;
		top: 0;
		left: 0;
	}
	.btn-default i {
		margin-right: 5px;
	}
	#wrapper {
	    min-height: 300px;
	    font-size: 14px;
	    position: absolute;
	    top: 50%;
	    margin-top: -150px;
	    background: #fff;
	    padding: 30px;
	    border-radius: 8px;
	  }
		#reg-form input,
		#reg-form button {
	    width: 60%;
	    margin-left: 20%;
	    margin-top: 10px;
	    height: 30px;
	  }
	  #reg-form .alert {
	  	width: 60%;
	  	margin-left: 20%;
	  	margin-top: 30px;
	  }

	  @media screen and (max-width: 992px) {
	    #wrapper {
	      padding: 20px 10px;
	    }
	    #wrapper h1 {
	      line-height: normal;
	    }
  }

  @media screen and (max-width: 768px) {
    #wrapper h1 {
      font-size: 2em;
      margin-bottom: 20px;
    }
  }
  @media screen and (max-width: 450px) {
    #wrapper h1 {
      font-size: 1.3em;
    }
  }
</style>


